
import { useState } from 'react';

export default function AddUserForm({ currentUser }) {
  const [form, setForm] = useState({ email: '', password: '', name: '', role: 'user' });
  const [message, setMessage] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    const res = await fetch('/api/add-user', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ ...form, adminId: currentUser?.id })
    });
    const data = await res.json();
    setMessage(data.message || ' ');
  };

  return (
    <div>
      <h2>   </h2>
      <form onSubmit={handleSubmit}>
        <input placeholder="" value={form.email} onChange={e => setForm({ ...form, email: e.target.value })} required />
        <input placeholder=" " type="password" value={form.password} onChange={e => setForm({ ...form, password: e.target.value })} required />
        <input placeholder="" value={form.name} onChange={e => setForm({ ...form, name: e.target.value })} required />
        <select value={form.role} onChange={e => setForm({ ...form, role: e.target.value })}>
          <option value="user"></option>
          <option value="admin"></option>
        </select>
        <button type="submit"></button>
      </form>
      {message && <p>{message}</p>}
    </div>
  );
}
